"use client";

import React, { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import Link from "next/link";
import { Search, Bell, Settings, User, Moon, Sun } from "lucide-react";

const Header = () => {
  const [theme, setTheme] = useState(
    typeof window !== "undefined" ? localStorage.getItem("theme") || "light" : "light"
  );

  useEffect(() => {
    if (theme === "dark") {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
    localStorage.setItem("theme", theme);
  }, [theme]);

  const toggleTheme = () => {
    setTheme(theme === "light" ? "dark" : "light");
  };

  return (
    <header className="fixed top-0 left-0 w-full bg-gray-900/80 dark:bg-black/80 backdrop-blur-md shadow-md z-50">
      <div className="container mx-auto px-6 py-3 flex justify-between items-center">
        {/* Logo */}
        <Link href="/" className="flex items-center gap-2 text-xl font-bold text-white">
          <span className="text-primary">MyBrand</span>
        </Link>

        {/* Search Bar */}
        <div className="relative hidden md:flex items-center w-[300px] bg-gray-800 dark:bg-gray-700 rounded-lg px-4 py-2">
          <Search size={16} className="text-gray-400" />
          <Input
            type="text"
            placeholder="Search"
            className="bg-transparent border-none outline-none text-white placeholder-gray-400 flex-1 px-2"
          />
          <span className="text-gray-400 text-sm px-2 py-1 rounded-md border border-gray-600">
            Ctrl + K
          </span>
        </div>

        {/* Actions */}
        <div className="flex items-center gap-3">
          <Button variant="ghost" className="p-2">
            <Settings size={18} className="text-white" />
          </Button>
          <Button variant="ghost" className="p-2">
            <Bell size={18} className="text-white" />
          </Button>

          {/* Theme Switcher */}
          <Button variant="ghost" className="p-2" onClick={toggleTheme}>
            {theme === "light" ? <Moon size={18} className="text-white" /> : <Sun size={18} className="text-yellow-400" />}
          </Button>

          {/* Profile Icon */}
          <Button variant="outline" className="p-2 rounded-full border-gray-700 dark:border-gray-500">
            {theme === "light" ? <User size={18} className="text-gray-900" />: <User size={18} className="text-white" />}

          </Button>

          {/* Upgrade Button */}
          <Button className="bg-pink-500 text-white px-4 py-2 rounded-lg hover:bg-pink-600">
            Upgrade to Plus
          </Button>
        </div>
      </div>
    </header>
  );
};

export default Header;
